/* stylelint-disable */
@font-face {
  font-family: "Popins-reg";
  src: url("../fonts/../fonts/Poppins-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Popins-semb";
  src: url("../fonts/../fonts/Poppins-SemiBold.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.burgundy-btn {
  color: #FFFFFF;
  border-radius: 8px;
  background: #691A2E;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.burgundy-btn:hover {
  background: #521424;
}

.orange-btn {
  border-radius: 8px;
  background: #E1712D;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  color: black;
  transition: background-color 0.3s ease;
}
.orange-btn:hover {
  background: #C26226;
}

.grey-btn {
  border-radius: 8px;
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.grey-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*::-webkit-scrollbar {
  width: 0;
  height: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

img,
picture {
  width: auto;
  height: auto;
  max-width: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}

.main {
  flex: 1 1 auto;
}

[class*=__container] {
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
}

body {
  font-family: "Popins-reg", sans-serif;
  font-style: normal;
  font-size: 20px;
  letter-spacing: 0.02em;
  color: #111111;
  background: transparent;
}
body._lock {
  overflow: hidden;
}

.casino-bonus {
  background: #FCEDF0;
  margin: 0 0 20px 0;
}
.casino-bonus._onWhite {
  border-right: 8px;
  padding: 15px;
}
@media screen and (max-width: 350px) {
  .casino-bonus._onWhite {
    padding: 5px;
  }
}
.casino-bonus__all-link {
  display: inline-block;
  font-family: "Popins-semb", sans-serif;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  align-self: center;
  text-align: center;
  padding: 19px 85px 17px;
}
.casino-bonus__container {
  display: flex;
  flex-direction: column;
}
.casino-bonus__row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.casino-bonus__title {
  margin: 0 0 10px 0;
}
.casino-bonus__text {
  font-size: 15px;
  line-height: 22px;
  color: #111111;
  max-width: 940px;
  margin: 0 0 20px 0;
}
.casino-bonus__tabs {
  margin: 0 0 30px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  background: rgba(255, 255, 255, 0.01);
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 8px;
}
.casino-bonus__tabs-item:not(:last-child) {
  margin: 0 5px 0 0;
}
.casino-bonus__tabs-wrap {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.casino-bonus__tabs-btn {
  display: inline-block;
  padding: 8px 18px 6px;
  font-size: 13px;
  line-height: 22px;
  color: #1D1C0C;
  border-radius: 8px;
  background: transparent;
  transition: background-color 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
}
.casino-bonus__tabs-btn:hover {
  color: #FFFFFF;
  background: #691A2E;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
}
.casino-bonus__tabs-btn._active {
  color: #FFFFFF;
  background: #691A2E;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
}
.casino-bonus__list {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 15px;
  margin: 0 0 35px 0;
  counter-reset: my-counter;
}
.casino-bonus__list._openItem {
  align-items: start;
}
.casino-bonus__list-wrapper {
  flex: 1 1 auto;
  margin: 0 40px 0 0;
  display: flex;
  flex-direction: column;
}
.casino-bonus__item {
  overflow: hidden;
  display: flex;
  max-width: 100%;
  flex-direction: column;
  background: #FFFFFF;
  width: 100%;
  align-items: center;
  border-radius: 12px;
}
.casino-bonus__item-link_wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.casino-bonus__item-link {
  margin: 0 0 5px 0;
}
.casino-bonus__item-sublink {
  display: inline-block;
  font-size: 10px;
  line-height: 11px;
  color: #808080;
  transition: color 0.3s ease;
}
.casino-bonus__item-sublink:hover {
  color: #111111;
}
.casino-bonus__item-wrapper {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 10px 20px 10px;
  border-bottom: 3px dotted #FCEDF0;
  position: relative;
}
.casino-bonus__item-wrapper::before, .casino-bonus__item-wrapper::after {
  position: absolute;
  pointer-events: none;
  width: 19px;
  height: 19px;
  background: #FCEDF0;
  border-radius: 50%;
  right: -11px;
}
.casino-bonus__item-image {
  min-width: 100%;
  max-width: 100%;
  margin: 0 0 15px 0;
  border-radius: 8px;
  position: relative;
}
.casino-bonus__item-image img {
  border-radius: 8px;
}
.casino-bonus__item-image::before {
  counter-increment: my-counter;
  content: counter(my-counter) " ";
  position: absolute;
  top: 0;
  left: 0;
  font-family: "Popins-semb", sans-serif;
  background: #691A2E;
  border-radius: 8px 0 8px 0;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  line-height: 20px;
  color: #FFFFFF;
}
.casino-bonus__item-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 0 15px 0;
  min-width: 164px;
}
.casino-bonus__item-title {
  font-family: "Popins-semb", sans-serif;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: -0.004em;
  color: #1D1C0C;
  margin: 0 0 8px 0;
}
.casino-bonus__item-licensed {
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.004em;
  color: #2275D4;
  display: flex;
  align-items: center;
  margin: 0 0 10px 0;
}
.casino-bonus__item-licensed-one {
  margin: 0 8px 0 0;
}
.casino-bonus__item-rating {
  display: flex;
  align-items: center;
  margin: 0 0 15px 0;
}
.casino-bonus__item-rating-star {
  margin: 0 10px 0 0;
  display: flex;
  align-items: center;
}
.casino-bonus__item-rating-star img {
  display: inline-block;
  margin: 0 6px 0 0;
}
.casino-bonus__item-rating-star-text {
  font-size: 12px;
  line-height: 12px;
  color: #111111;
  white-space: nowrap;
}
.casino-bonus__item-rating-comment {
  display: flex;
  align-items: center;
}
.casino-bonus__item-rating-comment img {
  display: inline-block;
  margin: 0 5px 0 0;
}
.casino-bonus__item-rating-comment-text {
  font-size: 12px;
  line-height: 12px;
  color: #111111;
  white-space: nowrap;
}
.casino-bonus__item-link {
  display: inline-block;
  font-size: 15px;
  line-height: 20px;
  color: #FFFFFF;
  padding: 12px 47px 10px;
}
.casino-bonus__item-reviews {
  display: none;
  align-items: flex-start;
  flex: 1 1 auto;
}
.casino-bonus__item-reviews._show {
  display: flex;
}
.casino-bonus__item-reviews-btn {
  display: inline-block;
  position: absolute;
  bottom: -17px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  padding: 7px 25px 5px;
  font-size: 13px;
  line-height: 20px;
  border-radius: 8px;
  color: #111111;
  background: #FCEDF0;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
}
.casino-bonus__item-textInfo {
  font-size: 12px;
  line-height: 28px;
  padding: 0 20px;
}
.casino-bonus__item-plus, .casino-bonus__item-minus {
  display: flex;
  flex-direction: column;
}
.casino-bonus__item-plus-txt, .casino-bonus__item-minus-txt {
  display: flex;
  align-items: flex-start;
  font-size: 12px;
  line-height: 28px;
  color: #808080;
}
.casino-bonus__item-plus-txt::before, .casino-bonus__item-minus-txt::before {
  content: "";
  display: inline-block;
  margin: 6px 8px 0 0;
  min-width: 16px;
  min-height: 16px;
}
.casino-bonus__item-plus {
  margin: 0 30px 0 0;
}
.casino-bonus__item-plus-txt::before {
  background: url("../img/home/best/arrow.svg") no-repeat 0% 0%/contain;
}
.casino-bonus__item-coupon {
  width: 100%;
  padding: 25px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: url(../img/home/best/coupon-bg-tab.svg) no-repeat 0 50%/cover;
}
.casino-bonus__item-coupon-title {
  font-family: "Popins-semb", sans-serif;
  font-size: 15px;
  line-height: 18px;
  color: #111111;
  margin: 0 0 5px 0;
}
.casino-bonus__item-coupon-subtitle {
  font-family: "Popins-semb", sans-serif;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: -0.004em;
  color: #111111;
  margin: 0 0 5px 0;
}
.casino-bonus__item-coupon-text {
  font-size: 13px;
  line-height: 18px;
  letter-spacing: -0.004em;
  color: #111111;
  margin: 0 0 10px 0;
}
.casino-bonus__item-coupon-link {
  width: 60%;
  text-align: center;
  display: inline-block;
  padding: 12px 28px 10px;
  color: black;
  font-size: 15px;
  line-height: 20px;
}

@media screen and (max-width: 1240px) {
  .casino-bonus__container {
    padding: 0;
  }
  .casino-bonus__title, .casino-bonus__text, .casino-bonus__row {
    padding: 0 15px;
  }
  .casino-bonus__tabs {
    margin: 0;
  }
  .casino-bonus__tabs-wrap {
    justify-content: flex-start;
    overflow-x: scroll;
    padding: 0 15px;
    margin: 0 0 25px 0;
  }
  .casino-bonus__list {
    display: grid;
    grid-template-columns: auto auto;
    gap: 15px;
  }
  .casino-bonus__item {
    min-width: 310px;
    max-width: 100%;
    flex-direction: column;
    background: #FFFFFF;
  }
  .casino-bonus__item:not(:last-child) {
    margin: 0;
  }
  .casino-bonus__item-wrapper {
    flex: 0 1 auto;
    flex-direction: column;
    padding: 10px 10px 20px 10px;
    border-right: none;
    border-bottom: 3px dotted #FCEDF0;
  }
  .casino-bonus__item-wrapper::before {
    right: -20px;
    bottom: -11px;
  }
  .casino-bonus__item-wrapper::after {
    top: auto;
    bottom: -11px;
    left: -20px;
  }
  .casino-bonus__item-info {
    align-items: center;
  }
  .casino-bonus__item-image {
    margin: 0 0 15px 0;
  }
  .casino-bonus__item-info {
    margin: 0 0 15px 0;
  }
  .casino-bonus__item-reviews {
    flex-direction: column;
    display: none;
  }
  .casino-bonus__item-reviews._show {
    display: flex;
  }
  .casino-bonus__item-reviews-btn {
    display: inline-block;
  }
  .casino-bonus__item-reviews-plus {
    margin: 0 0 20px 0;
  }
  .casino-bonus__item-coupon {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 25px 10px;
    background: url("../img/home/best/coupon-bg-tab.svg") no-repeat 0 50%/cover;
  }
  .casino-bonus__item-coupon-subtitle {
    text-align: center;
  }
  .casino-bonus__item-coupon-text {
    flex: 1 1 auto;
  }
  .casino-bonus__item-coupon-link {
    width: 60%;
    text-align: center;
  }
}
@media screen and (max-width: 1024px) {
  .casino-bonus {
    padding: 15px 0 0 0;
  }
  .casino-bonus__row {
    flex-direction: column;
    align-items: center;
  }
  .casino-bonus__list-wrapper {
    margin: 0 0 20px 0;
  }
  .casino-bonus__item {
    min-width: 100%;
    max-width: 100%;
  }
  .casino-bonus__item-image {
    max-width: 100%;
  }
  .casino-bonus__item-wrapper {
    padding: 10px 0 20px 0;
    width: 95%;
  }
}
@media screen and (max-width: 600px) {
  .casino-bonus__all-link {
    width: calc(100% - 30px);
  }
  .casino-bonus__text {
    font-size: 13px;
    line-height: 22px;
  }
  .casino-bonus__row {
    padding: 0 15px;
  }
  .casino-bonus__list {
    display: grid;
    grid-template-columns: auto;
    gap: 15px;
  }
  .casino-bonus__item {
    border-radius: 8px;
  }
  .casino-bonus__item-wrapper::before, .casino-bonus__item-wrapper::after {
    content: none;
  }
  .casino-online {
    width: 100%;
    border-radius: 0;
    padding: 0 15px;
  }
  .casino-online__title {
    font-size: 20px;
    line-height: 48px;
  }
}